.modal-wrapper {
  position: fixed;
  z-index: 2;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}

.modal-body {
  position: fixed;
  background: #050200;
  background: -webkit-gradient(linear, left bottom, left top, from(rgba(0, 0, 0, 0.81)), to(#01678f));
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.81) 0%, #01678f 100%);
  z-index: 3;
  width: 27.4rem;
  height: auto;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 15px;
}

.modal-close {
  padding: .5rem;
  border-radius: 5px;
  position: fixed;
  top: 0;
  right: 0;
}

.modal-title {
  color: teal;
}

.modal-close-butt {
  fill: #d30000;
  width: 1.5rem;
  padding: .4rem;
}
